"use client"

import type React from "react"

import { useEffect, useState, useRef } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Sparkles, Eye, EyeOff, Loader2, AlertCircle } from "lucide-react"
import { auth } from "@/lib/firebaseClient"
import { useAuth } from "@/lib/auth-context"
import { signInWithEmailAndPassword, signInWithPopup, GoogleAuthProvider } from "firebase/auth"
import { getPlayerOnboardingStatus } from "@/lib/players"

export function LoginPlayersForm() {
  const router = useRouter()
  const { user, loading: authLoading } = useAuth()
  const redirectHandledRef = useRef(false)

  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [isGoogleLoading, setIsGoogleLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [redirectTo, setRedirectTo] = useState("/players")

  useEffect(() => {
    setRedirectTo("/players")
  }, [])

  const createSessionCookie = async (idToken: string) => {
    const sessionRes = await fetch("/api/auth/session", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify({ idToken }),
    })

    if (!sessionRes.ok) {
      const data = await sessionRes.json().catch(() => null)
      if (data?.error) {
        throw new Error(data.error)
      }
      const text = await sessionRes.text().catch(() => "")
      throw new Error(text || "Failed to create session cookie")
    }
  }

  const handleAuthRedirect = async (uid: string, userEmail?: string | null) => {
    // Prevent double redirect
    if (redirectHandledRef.current) {
      console.info("[PlayersLogin] redirect already handled, skipping")
      return
    }
    redirectHandledRef.current = true

    console.info("[PlayersLogin] ========== AUTH REDIRECT START ==========")
    console.info("[PlayersLogin] user.uid:", uid)
    console.info("[PlayersLogin] user.email:", userEmail)

    try {
      const status = await getPlayerOnboardingStatus(uid)

      console.info("[PlayersLogin] ========== ONBOARDING STATUS ==========")
      console.info("[PlayersLogin] docPath:", status.docPath)
      console.info("[PlayersLogin] exists:", status.exists)
      console.info("[PlayersLogin] onboardingCompleted:", status.onboardingCompleted)
      console.info("[PlayersLogin] redirectTo:", redirectTo)

      console.info("[PlayersLogin] DECISION: Login always skips onboarding -> Redirect to:", redirectTo)
      router.replace(redirectTo)
    } catch (err) {
      console.error("[PlayersLogin] Error during redirect logic:", err)
      router.replace("/players")
    }
  }

  useEffect(() => {
    if (!authLoading && user && !redirectHandledRef.current) {
      handleAuthRedirect(user.uid, user.email)
    }
  }, [authLoading, user])

  const handleEmailLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setError(null)
    setIsLoading(true)

    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password)
      const idToken = await userCredential.user.getIdToken()
      await createSessionCookie(idToken)
      await handleAuthRedirect(userCredential.user.uid, userCredential.user.email)
    } catch (error: any) {
      if (error.code === "auth/user-not-found") {
        setError("No se encontró una cuenta con este email. Verifica que estés registrado.")
      } else if (error.code === "auth/wrong-password") {
        setError("Contraseña incorrecta. Inténtalo de nuevo.")
      } else if (error.code === "auth/invalid-email") {
        setError("Email inválido.")
      } else if (error.code === "auth/user-disabled") {
        setError("Esta cuenta ha sido deshabilitada.")
      } else if (error.code === "auth/too-many-requests") {
        setError("Demasiados intentos fallidos. Inténtalo más tarde.")
      } else {
        setError("Ocurrió un error al iniciar sesión. Por favor intenta de nuevo.")
      }
    } finally {
      setIsLoading(false)
    }
  }

  const handleGoogleLogin = async () => {
    setError(null)
    setIsGoogleLoading(true)

    try {
      const provider = new GoogleAuthProvider()
      const result = await signInWithPopup(auth, provider)
      const idToken = await result.user.getIdToken()
      await createSessionCookie(idToken)
      await handleAuthRedirect(result.user.uid, result.user.email)
    } catch (error: any) {
      if (error.code === "auth/popup-closed-by-user") {
        setError("Inicio de sesión cancelado.")
      } else if (error.code === "auth/popup-blocked") {
        setError("Popup bloqueado por el navegador. Permite popups para este sitio.")
      } else {
        setError("Ocurrió un error con Google. Por favor intenta de nuevo.")
      }
    } finally {
      setIsGoogleLoading(false)
    }
  }

  return (
    <Card className="w-full max-w-md backdrop-blur-sm bg-white/80 border-0 shadow-2xl">
      <CardHeader className="text-center pb-2">
        <Link href="/" className="inline-flex items-center gap-2 justify-center">
          <div className="w-10 h-10 rounded-xl bg-gradient-to-r from-blue-600 to-blue-500 flex items-center justify-center">
            <Sparkles className="w-5 h-5 text-white" />
          </div>
          <span className="font-bold text-2xl bg-gradient-to-r from-blue-600 to-blue-500 bg-clip-text text-transparent">
            courtly
          </span>
        </Link>
        <div className="mt-4">
          <CardTitle className="text-2xl font-bold text-gray-900">Bienvenido</CardTitle>
          <CardDescription className="text-gray-600 mt-1">
            Inicia sesión para reservar, encontrar partidos y jugar más
          </CardDescription>
        </div>
      </CardHeader>
      <CardContent className="space-y-6">
        {error && (
          <Alert variant="destructive">
            <AlertCircle className="h-4 w-4" />
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <Button
          onClick={handleGoogleLogin}
          disabled={isGoogleLoading}
          className="w-full bg-white hover:bg-gray-50 text-gray-900 border border-gray-300 shadow-sm"
          variant="outline"
        >
          <svg className="w-5 h-5 mr-2" viewBox="0 0 24 24">
            <path
              fill="currentColor"
              d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
            />
            <path
              fill="currentColor"
              d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
            />
            <path
              fill="currentColor"
              d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
            />
            <path
              fill="currentColor"
              d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
            />
          </svg>
          {isGoogleLoading ? "Conectando con Google..." : "Continuar con Google"}
        </Button>

        <div className="relative">
          <div className="absolute inset-0 flex items-center">
            <span className="w-full border-t border-gray-300" />
          </div>
          <div className="relative flex justify-center text-sm">
            <span className="px-2 bg-white text-gray-500">O continúa con email</span>
          </div>
        </div>

        <form onSubmit={handleEmailLogin} className="space-y-4">
          <div>
            <Label htmlFor="email" className="text-gray-700">Email</Label>
            <Input
              id="email"
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              className="mt-1 bg-white border-gray-300 text-gray-900"
              placeholder="tu@email.com"
              disabled={isLoading || isGoogleLoading}
            />
          </div>
          <div>
            <Label htmlFor="password" className="text-gray-700">Contraseña</Label>
            <div className="relative mt-1">
              <Input
                id="password"
                type={showPassword ? "text" : "password"}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                className="pr-10 bg-white border-gray-300 text-gray-900"
                placeholder="Tu contraseña"
                disabled={isLoading || isGoogleLoading}
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-400 hover:text-gray-600"
              >
                {showPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </button>
            </div>
          </div>
          <Button
            type="submit"
            className="w-full bg-gradient-to-r from-blue-600 to-blue-500 hover:from-blue-700 hover:to-blue-600 text-white shadow-lg"
            disabled={isLoading || isGoogleLoading}
          >
            {isLoading ? (
              <>
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                Iniciando sesión...
              </>
            ) : (
              "Iniciar sesión"
            )}
          </Button>
        </form>

        <div className="text-center">
          <Link href="/players/signup" className="text-sm text-blue-600 hover:text-blue-500">
            ¿No tienes cuenta? Regístrate
          </Link>
        </div>
      </CardContent>
    </Card>
  )
}
